import React, { useState } from "react";
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import "./Login.css";

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

const Login = ({ onLogin }) => {
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showChangePassword, setShowChangePassword] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  // useEffect  .

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMessage("");
    try {
      const response = await axios.post(`${API_URL}/api/login`, { name, password });
      console.log('Login response:', response.data);
      
      if (password === '0000') {
        console.log('Initial password detected, showing password change form');
        setShowChangePassword(true);
      } else {
        console.log('Normal login, proceeding to main page');
        onLogin(response.data.name, response.data.role);
        if (response.data.role === 'admin') {
          navigate('/admin');
        } else {
          navigate(`/worker/${response.data.name}`);
        }
      }
    } catch (error) {
      console.error('   :', error);
      if (error.response) {
        //    
        setErrorMessage(error.response.data.error || " .");
      } else if (error.request) {
        //      
        setErrorMessage("   .   .");
      } else {
        //      
        setErrorMessage("    .");
      }
    }
  };

  const handlePasswordChange = async (e) => {
    e.preventDefault();
    if (newPassword !== confirmPassword) {
      setErrorMessage("   .");
      return;
    }
    try {
      const response = await axios.post(`${API_URL}/api/change-password`, { name, newPassword });
      if (response.data.message === "  .") {
        alert(" .    .");
        setShowChangePassword(false);
        setPassword("");
        setNewPassword("");
        setConfirmPassword("");
        navigate('/login');
      }
    } catch (error) {
      console.error('    :', error);
      if (error.response) {
        setErrorMessage(error.response.data.error || "  .");
      } else if (error.request) {
        setErrorMessage("   .   .");
      } else {
        setErrorMessage("     .");
      }
    }
  };

  return (
    <div className="login-container">
      <div className="login-box">
        <h2>{showChangePassword ? " " : ""}</h2>
        {showChangePassword ? (
          <form onSubmit={handlePasswordChange}>
            <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder=" "
              required
            />
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="  "
              required
            />
            <button type="submit"> </button>
          </form>
        ) : (
          <form onSubmit={handleSubmit}>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder=""
              required
            />
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=""
              required
            />
            {errorMessage && <p style={{ color: "red" }}>{errorMessage}</p>}
            <button type="submit"></button>
          </form>
        )}
      </div>
    </div>
  );
};

export default Login;
